@use 'assets/styles/mixins' as mixins;
.list {
    background-color: white;
    padding: 20px;
    border-radius: var(--border-radius);
    border: var(--border);
    flex: 1 1 auto;
}

.li {
    padding: 5px 0px;
    border-bottom: var(--border);
    @include mixins.xs {
        flex-direction: column;
        a {
            width: 100%;
        }
        span, button {
            margin-bottom: 10px;
            width: 100%;
        }
    }
}